import React, { PureComponent, PropTypes } from 'react';
import classNames from 'classnames/bind';

import '../../styles/global.scss';

import styles from './CreditCard.scss';
const cx = classNames.bind(styles);

// FIXME: auto complete than card num input 1~4 still undefined
// TODO: chrome auto complete remember style

import flipCard from './animations/flipCard.js';

const inputs_setting = {
  "cardNumber": {
    maxLength: 4,
    nextInputId: "xxx2"
  },
  "xxx2": {
    maxLength: 4,
    nextInputId: "xxx3"
  },
  "xxx3": {
    maxLength: 4,
    nextInputId: "xxx4"
  },
  "xxx4": {
    maxLength: 4,
    nextInputId: "cardExpirationMonth"
  },
  "cardExpirationMonth": {
    maxLength: 2,
    nextInputId: "card_exp_year"
  },
  "card_exp_year": {
    maxLength: 4,
    nextInputId: "card_holder_name",
    cb(cb) {
      cb();
    }
  },
  "card_holder_name": {
    maxLength: 60,
    nextInputId: "false"
  },
  "cardCVC": {
    maxLength: 3,
    nextInputId: false,
  }
}

class CreditCard extends PureComponent {
  constructor() {
    super();
    this.state = {
      cardNumber: "",
      xxx2: "",
      xxx3: "",
      xxx4: "",
      cardExpirationMonth: "",
      card_exp_year: "",
      card_holder_name: "",
      cardCVC: "",
    }
  }
  componentDidMount() {}



  _flipCard = () => {
    flipCard(this.cardContainerNode)
  }


  /**
   * when onChange happens in first card num input
   * because it support for mobile scan
   * so when it get number of length large then four
   * automatic separate it to four inputs
   * 
   * @memberof CreditCard
   */
  _onChangeCardInput = (event) => {
    const inputSetting = inputs_setting[event.target.id];
    const input_val = event.target.value;

    // for mobile camera credit card scan
    // 1. for first cardNumber input
    // 2. only trigger when over 4 num situation(for mobile 1x scan)
    // 3. prevent invalid lock in 4th num and try to input 5th num
    if (event.target.id == "cardNumber" && input_val.length > 4 && !this.state[`${event.target.id}__invalid`]) {
      const scan_array = input_val.match(/.{1,4}/g);
      this.setState({
        cardNumber: scan_array[0],
        xxx2: scan_array[1],
        xxx3: scan_array[2],
        xxx4: scan_array[3],
        cardNumber__invalid: false,
        xxx3__invalid: !scan_array[1].length == 4,
        xxx2__invalid: !scan_array[2].length == 4,
        xxx4__invalid: !scan_array[3].length == 4,
      })
    } else if (input_val.length <= inputSetting.maxLength) { //
      this.setState({
        [event.target.id]: input_val
      });
    }

    // input done
    if (input_val.length === inputSetting.maxLength || event.target.id === "card_holder_name") {
      // valid check
      const reg_input = new RegExp(event.target.pattern)
      const input_invalid = !reg_input.test(input_val);
      console.log(event.target.id);
      // focus to next input field, and try to call callback from setting 
      if (!input_invalid) {
        const next_input = document.getElementById(inputSetting.nextInputId);
        next_input && next_input.focus();

        // input valid cb
        inputSetting.cb && inputSetting.cb(this._flipCard)
        this.setState({
          [`${event.target.id}__invalid`]: input_invalid
        }, () => {
          this._checkAllInputValid();
        })
      } else {
        this.setState({
          [`${event.target.id}__invalid`]: input_invalid
        })
      }
    }
  }

  _checkAllInputValid = () => {
    const allInputValid = Object.keys(inputs_setting).reduce((acc, id) => {
      console.log(`${id}__invalid`);
      console.log(this.state[`${id}__invalid`]);
      return acc && this.state[`${id}__invalid`] === false;
    }, true)
    console.log(allInputValid);
    this.setState({
      all_valid: allInputValid
    })
  }

  onSubmit = (event) => {
    event.preventDefault();
  }

  render() {
    const {all_valid} = this.state;
    return (
      <div className={ styles.container }>
        <form
          ref={ form => this.cardContainerNode = form }
          onSubmit={ this.onSubmit }
          className={ styles.card + ` active` }>
          <div className={ cx("card__front", "card") }>
            <i
              onClick={ this._flipCard }
              className={ styles.btn__turnOver + " fa fa-repeat" }
              aria-hidden="true"></i>
            <h1>PAYMENT DETAILS</h1>
            <div className={ styles.front__inputs }>
              <label htmlFor="cardNumber" className={ styles.block__munDraC }>
                <h3>CARD NUMBER</h3>
                <input
                  id="cardNumber"
                  className={ cx("input__style", {
                                "input__invalid": this.state['cardNumber__invalid']
                              }) }
                  onChange={ this._onChangeCardInput }
                  value={ this.state.cardNumber }
                  name="cardnumber"
                  pattern="[0-9]{4}"
                  type="text"
                  autoComplete="cc-number"
                  required/>
                <input
                  id="xxx2"
                  className={ cx("input__style", {
                                "input__invalid": this.state['xxx2__invalid']
                              }) }
                  onChange={ this._onChangeCardInput }
                  value={ this.state.xxx2 }
                  type="number"
                  pattern="[0-9]{4}"
                  autoComplete="off" />
                <input
                  id="xxx3"
                  className={ cx("input__style", {
                                "input__invalid": this.state['xxx3__invalid']
                              }) }
                  onChange={ this._onChangeCardInput }
                  value={ this.state.xxx3 }
                  type="number"
                  pattern="[0-9]{4}"
                  autoComplete="off" />
                <input
                  id="xxx4"
                  className={ cx("input__style", {
                                "input__invalid": this.state['xxx4__invalid']
                              }) }
                  onChange={ this._onChangeCardInput }
                  value={ this.state.xxx4 }
                  type="number"
                  pattern="[0-9]{4}"
                  autoComplete="off" />
              </label>
              <label className={ styles.label__expiration } htmlFor="cardExpirationMonth">
                <h3>EXPIRATION DATE</h3>
                <input
                  id="cardExpirationMonth"
                  name="card_month"
                  className={ cx("input__style", {
                                "input__invalid": this.state['cardExpirationMonth__invalid']
                              }) }
                  onChange={ this._onChangeCardInput }
                  value={ this.state.cardExpirationMonth }
                  type="text"
                  pattern="[0-9]{2}"
                  placeholder="MM"
                  autoComplete="cc-exp-month"
                  size="2"
                  maxLength="2"
                  required/>
                <input
                  id="card_exp_year"
                  name="card_year"
                  className={ cx("input__style", {
                                "input__invalid": this.state['card_exp_year__invalid']
                              }) }
                  onChange={ this._onChangeCardInput }
                  value={ this.state.card_exp_year }
                  type="text"
                  pattern="[0-9]{4}"
                  placeholder="YYYY"
                  autoComplete="cc-exp-year"
                  size="4"
                  maxLength="4"
                  required/>
              </label>
            </div>
          </div>
          <div className={ cx("card__back", "card") }>
            <i
              onClick={ this._flipCard }
              className={ styles.btn__turnOver + " fa fa-repeat" }
              aria-hidden="true"></i>
            <label className={ styles.label__card_holder_name } htmlFor="card_holder_name">
              <h3>CARD HOLDER</h3>
              <input
                id="card_holder_name"
                name="card_holder_name"
                type="text"
                onChange={ this._onChangeCardInput }
                value={ this.state.card_holder_name }
                pattern="[\w\s]{3,}"
                autoComplete="cc-name"
                className={ cx("input__style", "input__card_holder_name", {
                              "input__invalid": this.state['card_holder_name__invalid']
                            }) }
                required></input>
            </label>
            <label className={ styles.label__cardCVC } htmlFor="cardCVC">
              <h3>CVC</h3>
              <input
                id="cardCVC"
                name="cvc"
                type="number"
                pattern="[0-9]{3,}"
                onChange={ this._onChangeCardInput }
                value={ this.state.cardCVC }
                maxLength="4"
                minLength="3"
                size="3"
                className={ cx("input__style", "input__cardCVC", {
                              "input__invalid": this.state['cardCVC__invalid']
                            }) }
                required></input>
            </label>
          </div>
        </form>
        <button type="submit" className={ cx("btn__submit", {
                                            "btn__submit--active": all_valid
                                          }) }>
          Pay $500
        </button>
      </div>
      );
  }
}

CreditCard.propTypes = {

};

export default CreditCard;



