import { TweenMax, Power0 } from "gsap";

export default function flipCard(dom) {
  TweenMax.to(dom, 1, {
    bezier: [{
      x: 0,
      z: 0,
      rotationY: "+=0"
    }, {
      x: -300,
      z: 50,
      rotationY: "+=90"
    }, {
      x: -110,
      z: 100,
      rotationY: "+=150"
    }, {
      x: 0,
      z: 0,
      rotationY: "+=180"
    }],
    ease: Power1.easeInOut
  })
}



